.login-container {
  display: flex;
  height: 100vh;
  background-color: #f5f5f5;
}

.login-left,
.login-right {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-left {
  background-color: #fff;
}

.login-form {
  width: 100%;
  max-width: 400px;
  padding: 20px;
  text-align: center;
}

.logo {
  width: 80px;
  height: 80px;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 20px;
  text-align: left;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

input[type='text'],
input[type='email'],
input[type='password'] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.btn {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 10px;
}

.login-btn {
  background-color: var(--primary-color); /* Ensure --primary-color is defined */
  color: #fff;
}

.google-login-btn {
  width: 100%;
  margin-bottom: 10px;
  background: #db4a39;
  color: #fff;
  border: none;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
  font-size: 16px;
}

.switch-btn {
  background-color: #6c757d; /* Neutral color for switch button */
  color: #fff;
}

.login-right {
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color)); /* Ensure --primary-color and --secondary-color are defined */
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
}

.login-right h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.login-right p {
  font-size: 1.2rem;
}
